/* eslint-disable no-unused-vars */
import "./App.css";
import { useState, useEffect, lazy, Suspense } from "react";
import Cookies from "js-cookie";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import { Loading } from "./components";
/*Redux*/
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "./state/index";
/***** */
const Dashboard = lazy(() => import("./pages/jsx/Dashboard"));
const Authentication = lazy(() => import("./pages/jsx/Authentication"));
const PayemntPage = lazy(() => import("./pages/jsx/PayemntPage"));
const Footer = lazy(() => import("./pages/jsx/Footer"));
const Team = lazy(() => import("./pages/jsx/Team"));
const Console = lazy(() => import("./pages/jsx/Console"));
const History = lazy(() => import("./pages/jsx/History"));
const ResetPassword = lazy(() => import("./pages/jsx/ResetPassword"));
const Account = lazy(() => import("./pages/Account/jsx/Account"));
const ViewUserInfo = lazy(() => import("./pages/Account/jsx/ViewInof"));
const Home = lazy(() => import("./pages/jsx/Home/Home"));
const ConsolPage1 = lazy(() => import("./pages/Console/page1"));

function App() {
  const state = useSelector((state) => state.User);

  const dispatch = useDispatch();

  const { SetUserInfo, Login } = bindActionCreators(actionCreators, dispatch);

  const ReadCoockie = async () => {
    const user = Cookies.get("user");
    if (user) {
      Login();
      const Req = await fetch(
        "https://generateplatformpdf.herokuapp.com/Authentication/GetUser",
        {
          method: "POST",
          headers: new Headers({
            autherization: "Bearer " + user,
            "Content-Type": "application/json",
          }),
        }
      );
      const jsondata = await Req.json();
      console.log(jsondata);
      SetUserInfo({
        username: jsondata.Username,
        Email: jsondata.Email,
        Profile: "https://uifaces.co/our-content/donated/XdLjsJX_.jpg",
        Subscription: jsondata.Subscription,
      });
    }
  };
  useEffect(() => {
    ReadCoockie();
  }, []);
  return (
    <div className='App'>
      <Routes />
    </div>
  );
}

const PertectedLogin = ({ auth, component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={() =>
        !auth ? (
          <>
            <Component />
          </>
        ) : (
          <Redirect to='/' />
        )
      }
    />
  );
};

const Routes = (props) => {
  const state = useSelector((state) => state.User);
  state.isloged = true;
  return (
    <Router basename='/PlatformX'>
      <Suspense fallback={<Loading />}>
        {" "}
        <Switch>
          <PertectedLogin
            path='/login'
            auth={state.isloged}
            component={() => <Authentication />}
          />{" "}
          <Route path={`/Config_account`} component={() => <ResetPassword />} />{" "}
          <Route path={`/Subscribtion`} component={() => <PayemntPage />} />{" "}
          <Route
            path={"/Console"}
            render={({ match: { path } }) => (
              <Console auth={state.isloged} Subscription={state.Subscription}>
                <Switch>
                  <Route
                    exact
                    path={path + "/"}
                    component={() => <ConsolPage1 />}
                  />{" "}
                  <Route
                    path={`${path}/tab1`}
                    component={() => <> Bientôt disponible... </>}
                  />
                  <Route
                    path={`${path}/tab2`}
                    component={() => <> Bientôt disponible... </>}
                  />
                  <Route
                    path={`${path}/tab3`}
                    component={() => <> Bientôt disponible... </>}
                  />
                  <Route
                    path={`${path}/tab4`}
                    component={() => <> Bientôt disponible... </>}
                  />
                  <Redirect exact from={path + "/*"} to={path} />{" "}
                </Switch>{" "}
              </Console>
            )}
          />{" "}
          <Route
            path={"/"}
            render={({ match: { path } }) => (
              <Home auth={state.isloged}>
                <Switch>
                  <Route exact path={"/"} component={() => <Dashboard />} />{" "}
                  <Route path={`${path}team`} component={() => <Team />} />{" "}
                  <Route
                    path={`${path}Histroy`}
                    component={() => <History />}
                  />{" "}
                  <Route path={`${path}about`} component={() => <Footer />} />{" "}
                  <Route
                    path={`${path}Profile`}
                    render={({ match: { path } }) => (
                      <Account>
                        <Switch>
                          <Route
                            exact
                            path={path + "/"}
                            component={() => <ViewUserInfo />}
                          />{" "}
                          <Route
                            path={`${path}/Edite`}
                            component={() => <> page2 </>}
                          />
                          <Route
                            path={`${path}/upgrade_subscription`}
                            component={() => <> upgrade subscription Page </>}
                          />
                          <Redirect exact from={path + "/*"} to={path} />{" "}
                        </Switch>{" "}
                      </Account>
                    )}
                  />{" "}
                  {/* <Redirect exact from={path + "*"} to={path} />{" "} */}{" "}
                </Switch>{" "}
              </Home>
            )}
          />{" "}
          <Route path='*' component={() => <div> 404 Not found </div>} />
        </Switch>{" "}
      </Suspense>{" "}
    </Router>
  );
};

export default App;
