export const SetUserInfo = ({ username, Email, Profile, Subscription }) => {
  return (dispatch) => {
    dispatch({
      type: "SetUserInfo",
      payload: {
        username,
        Email,
        Profile,
        Subscription,
      },
    });
  };
};
export const Login = () => {
  return (dispatch) => {
    dispatch({
      type: "login",
      payload: true,
    });
  };
};
export const UpdateSubscriptionPlane = () => {
  return (dispatch) => {
    dispatch({
      type: "Subscription",
      payload: 1,
    });
  };
};
export const logout = () => {
  return (dispatch) => {
    dispatch({
      type: "logout",
      payload: false,
    });
  };
};
