import { useState, useEffect, useContext } from "react";
import { logo } from "../../Assets";
import style from "../css/NavBar.module.css";
import * as IconAi from "react-icons/ai";
import { stateContext } from "../../StateContext";
import Cookies from "js-cookie";
import { NavLink, withRouter, useRouteMatch } from "react-router-dom";

function NavBar({ history }) {
  // let [Logedin, setLogin] = useContext(stateContext);
  const { url } = useRouteMatch();
  return (
    <div className={`${style.menu_parent_wrapper}`}>
      <div className={style.logo_wrapper}>
        <NavLink to="/">
          <img src={logo} alt="" srcSet="" />
        </NavLink>{" "}
      </div>{" "}
      <div className={style.menu_wrapper}>
        <ul>
          <li>
            <NavLink to={`/Histroy`} activeClassName={style.checked}>
              <p> History </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink to={`/about`} activeClassName={style.checked}>
              <p> Contact us </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink to={`/team`} activeClassName={style.checked}>
              <p> About </p>{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink to={`/Profile`} className={`${style.Profile}`}>
              <IconAi.AiOutlineUser className={style.icon} />{" "}
            </NavLink>{" "}
          </li>{" "}
        </ul>{" "}
      </div>{" "}
    </div>
  );
}

export default withRouter(NavBar);
