import React from "react";
import style from "../css/PaymentItem.module.css";
import * as IconBi from "react-icons/bi";

/*Redux*/

import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../../state/index";
import { withRouter } from "react-router-dom";
/***** */
// import * as Icon from "react-icons/ri";

function Payment_item({ icon, history }) {
  const state = useSelector((state) => state.User);

  const dispatch = useDispatch();

  const { UpdateSubscriptionPlane } = bindActionCreators(
    actionCreators,
    dispatch
  );

  return (
    <div className={style.Itemwrapper}>
      <div className={style.icon_wrapper}>
        <div className={style.Icon}> {icon} </div>{" "}
      </div>{" "}
      <div className={style.price}>
        <p> 300 dh / mo </p>{" "}
      </div>{" "}
      <ul className={style.ulItem}>
        <li>
          <IconBi.BiCheck className={style.liIcon} /> <p> Lorem, ipsum. </p>{" "}
        </li>{" "}
        <li>
          <IconBi.BiCheck className={style.liIcon} /> <p> Lorem, ipsum. </p>{" "}
        </li>{" "}
        <li>
          <IconBi.BiCheck className={style.liIcon} /> <p> Lorem, ipsum. </p>{" "}
        </li>{" "}
        <li>
          <IconBi.BiCheck className={style.liIcon} /> <p> Lorem, ipsum. </p>{" "}
        </li>{" "}
        <li>
          <IconBi.BiCheck className={style.liIcon} /> <p> Lorem, ipsum. </p>{" "}
        </li>{" "}
      </ul>{" "}
      <input
        className={style.start}
        type="button"
        value="GET STARTED"
        onClick={() => {
          UpdateSubscriptionPlane();
          history.push("/Console");
          console.log(state.Subscription);
        }}
      />{" "}
    </div>
  );
}

export default withRouter(Payment_item);
